.cm-hero__content{
    min-height: 50vh;
    background-color: rgb(29, 29, 29);
    display: flex;
    align-items: flex-end;
    padding-bottom: 3rem;
    position: relative;
    padding-top: 14rem;
    width: 100%;
    height: 100%;
}

.cm-hero__heading {
    color: #fff;
    max-width: 535px;
    margin-bottom: 3rem;
    z-index: 3;
    position: relative;
}

.cm-hero__content-wraper{
    position: relative;
    z-index: 3;
}

.media-upload-btn {
    background-color: transparent;
    border: none;
    padding: 0 .5rem;
    cursor: pointer;
}

.cm-hero_img {
    position: absolute;
    object-fit: cover;
    width: 100%;
    height: 100% !important;
    top: 0;
    z-index: 1;
  }

.cm-hero__overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 2;
    background: #000;
    top: 0;
}

@media screen and (min-width:1030px) {
    .cm-hero__content{
        min-height: 100vh;
        padding-bottom: 10rem;
    }
    .cm-hero__heading{
        max-width: 700px;
    }
}   

@media screen and (min-width:1400px) {
    .cm-hero__heading{
        max-width: 800px;
    }
}